<template>
    <!--      -->
    <div>
        <!--   -->
        <div class="searchWindow">
            <form @submit.prevent="goToSearchwithKeyword">
                <!--  -->
                <div class="input-group">
                    <input id="searchForm" class="form-control" type="text" v-model.trim="searchKeyword">
                    <input id="submitButton" type="submit" value="">
                </div>

                <!--  -->
                <div id="hashTagkeyword">
                    <router-link id="hash" :to="{
                        name: 'searchKeyword',
                        params: {
                            keyword: tag
                        }
                    }" v-for="(tag, index) in hashTag" :key="index">
                        # {{ tag }}
                    </router-link>
                </div>
            </form>
        </div>

        <!--    -->
        <DetailSearch class="detailSearch" />

        <!--    -->
        <SearchResult />
    </div>
</template>

<script>
import DetailSearch from './detailSearch.vue'
import SearchResult from './searchResult.vue'

export default {
    components: {
        DetailSearch,
        SearchResult,
    },

    //  vuex  
    data() {
        return {
            keyWord : "",
            searchKeyword : "",
            hashTag : ["", "", "", "", "", ""],
        }
    },
    methods: {
        //     
        goToSearchwithKeyword() {
            //    
            this.keyWord = ""
            const tempKeyword = this.searchKeyword
            tempKeyword.toLowerCase()
            console.log(tempKeyword)
            if(tempKeyword == "" || tempKeyword == "null") {
                this.$router.push({
                    name: 'search',
                })
            }
            else {
                this.$router.push({
                name: "searchKeyword",
                params: { keyword : tempKeyword }
                })
            }

            //  
            this.keyWord = this.searchKeyword
            this.searchKeyword = ""
        }
    }
    
}
</script>

<style>
/*  */
#hashTagkeyword {
    display: flex;
    justify-content: center;
}
#hash {
    margin: 1rem;
    background-color: #FD7E14;
    color: white;
    width: 5rem;
    padding: 0.6rem;
    border-radius: .5rem;
    text-decoration-line: none;
}
</style>

