<template>
  <!--     -->
  <div class="modal fade modal-xl" id="seasoningModalToggle" aria-hidden="true" aria-labelledby="seasoningModalToggleLabel" tabindex="-1">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h1 class="modal-title fs-5" id="exampleModalToggleLabel"> </h1>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>

          <!--     -->
          <div class="modal-body">
            <ul class="ListShow">
              <li class="ingredientList row" v-for="(seasoning, index) in seasoningList" :key="index">
                <p class="col-2">{{seasoning.name}}</p>
                <p class="col-4"> : {{seasoning.startDate}}</p>
                <p class="col-3">
                   : {{seasoning.endDate}}
                </p>
                <p class="col-2"> : {{seasoning.storage}}</p>
                <button class="col-1" @click="deleteSeasoning(seasoning)"></button>
              </li>
            </ul>
          </div>

          <!--    -->
          <div class="modal-body inputComponent">
            <div>
              <form @submit.prevent="goToSearchwithKeyword">
                <div class="input-group">
                    <input id="searchForm ingredientText" class="form-control" type="text" v-model.trim="ingredientName">
                    <input id="submitButton" type="submit" value="">
                </div>
              </form>
            </div>

            <!--     -->
            <div>
              <div>
                <label class="radioButton">
                  <input type="radio" name="classification" value="" v-model="selectStorage" @click="changeStorage">
                </label>
                <label class="radioButton">
                  <input type="radio" name="classification" value="" v-model="selectStorage" @click="changeStorage">
                </label>
                <label class="radioButton">
                  <input type="radio" name="classification" value="" v-model="selectStorage" @click="changeStorage">
                </label>
              </div>
              <button class="soundButton" data-bs-target="#seasoningModalToggle2" data-bs-toggle="modal"></button>
              <button class="soundButton" @click="appendList"></button>
            </div>
          </div>

          <!--     -->
          <div class="modal-footer">
            <button class="soundButton" @click="pushSeasoningData"></button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
    name: 'SeasoningText',
    data() {
      return {
        seasoningList : [{name : "", startDate : '2023-07-27', endDate: '', storage: ''}, 
        {name : "",  startDate : '2023-07-27', endDate: '', storage: ''},
        {name : "",  startDate : '2023-07-27', endDate: '', storage: ''},
        {name : "",  startDate : '2023-07-27', endDate: '', storage: ''},],
        seasoningName: '',
        selectStorage : '',
      }
  },
  methods: {
    appendList() {
      let today = new Date();   

      let year = today.getFullYear(); // 
      let month = today.getMonth() + 1;  // 
      let date = today.getDate();  // 

      this.seasoningList.push({name: this.seasoningName, startDate : `${year}-${month}-${date}`, endDate: '', storage: this.selectStorage})

      this.seasoningName = ''
      this.selectStorage = ''
    },
    pushSeasoningData() {
      console.log(this.seasoningList)
      this.seasoningList = []
    },
    deleteSeasoning(seasoning) {
            const arrayRemove = (arr, value) => {
                return arr.filter((ele) => {
                    return ele != value
                })
            }
            this.seasoningList = arrayRemove(this.seasoningList, seasoning)
        }

}
}
</script>

