<template>
    <!--  (,  )  -->
    <div class="myPage">
        <!--     -->
        <CategoryComponent />

        <!--    -->
        <div id="myPageView">
            <!--   & ,    -->
            <div class="buttonGroup">
                <!--     -->
                <div class="storageRadio">
                    <label class="radioButton">
                        <input type="radio" name="coldStorage" value="" v-model="storageType" @click="changeClassification">
                    </label>
                    <label class="radioButton">
                        <input type="radio" name="frozenStorage" value="" v-model="storageType" @click="changeClassification">
                    </label>
                    <label class="radioButton">
                        <input type="radio" name="rtStorage" value="" v-model="storageType" @click="changeClassification">
                    </label>            
                </div>

                <!-- ,    -->
                <div class="modalButtons">
                    <IngredientModal />
                    <SeasoningModal />
                </div>
            </div>
            
            <!--     &   (, , ) -->
            <div>
                <!--  -->
                <div class="refrigeratorCategory">
                    <p class="categoryTitle">{{storageType}} </p>
                    <ul class="ListShow">
                        <li class= "row" v-for="tmpingredient in ingredients" :key="tmpingredient.pk">
                            <div class="ingredientList" v-if="tmpingredient.storage === storageType">
                                <p class="col-1 ingredientName">{{tmpingredient.name}}</p>
                                <div class="amount col-2 row">
                                    <button class="amountButton col-3" @click="plusAmount(tmpingredient)">+</button>
                                    <p class="col-6">{{tmpingredient.amount}}{{tmpingredient.unit}}</p>
                                    <button class="amountButton col-3" @click="minusAmount(tmpingredient)">-</button>
                                </div>
                                <p class="col-3"> : {{tmpingredient.startDate}}</p>
                                <p class="col-3">
                                 : {{tmpingredient.endDate}}
                                </p>
                                <p class="col-2"> : {{tmpingredient.storage}}</p>
                                <button class="col-1 deleteButton" @click="deleteIngredient(tmpingredient)"></button>
                            </div>
                        </li>
                    </ul>
                </div>

                <!--  -->
                <div>
                    <p class="categoryTitle">{{storageType}} </p>
                    <ul class="ListShow">
                        <li class= "row" v-for="(seasoning, index) in seasonings" :key="index">
                            <div class="ingredientList" v-if="seasoning.storage === storageType">
                                <p class="col-2 ingredientName">{{seasoning.name}}</p>
                                
                                <p class="col-4"> : {{seasoning.startDate}}</p>
                                <p class="col-3">
                                 : {{seasoning.endDate}}
                                </p>
                                <p class="col-2"> : {{seasoning.storage}}</p>
                                <button class="col-1 deleteButton" @click="deleteSeasoning(seasoning)"></button>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
import CategoryComponent from './categoryComponent.vue'
import IngredientModal from '../modalPage/IngredientModal.vue'
import SeasoningModal from '../modalPage/SeasoningModal'

import {mapGetters, mapActions} from 'vuex'

export default {
    name: 'RefrigeratorPage',
    components: {
        CategoryComponent,
        IngredientModal,
        SeasoningModal,
    },
    computed: {
        ...mapGetters(['ingredients', 'seasonings']),
        ...mapActions(['plusAmount', 'minusAmount'])
    },
    //   
    data() {
        return {
            storageType : '',
        }
    },
    methods: {

        minusAmount(tmpingredient) {
            if(tmpingredient.unit === "g") {
                tmpingredient.amount -= 10
            }
            else{
                tmpingredient.amount --
            }

            if (tmpingredient.amount <= 0) {
                const arrayRemove = (arr, value) => {
                    return arr.filter((ele) => {
                        return ele != value
                    })
                }
                this.ingredients = arrayRemove(this.ingredients, tmpingredient)
            }
        },
        deleteIngredient(tmpingredient) {
            const arrayRemove = (arr, value) => {
                return arr.filter((ele) => {
                    return ele != value
                })
            }
            this.ingredients = arrayRemove(this.ingredients, tmpingredient)
        },
        deleteSeasoning(seasoning) {
            const arrayRemove = (arr, value) => {
                return arr.filter((ele) => {
                    return ele != value
                })
            }
            this.seasonings = arrayRemove(this.seasonings, seasoning)
        }
    }
}
</script>

<style scoped>
/*   &   */
.buttonGroup {
    display: flex;
    justify-content: space-between;
    width: 90%;
}

.modalButtons {
    display: flex;
}

.ListShow {
    border: solid #a7a7a7;
    border-radius: .5rem;
    width: 80%;
    margin: auto;
    padding: 1rem;
    margin-bottom: 5rem;
}

/*  ingredientName  */
.ingredientName {
font-weight: bold;
}

.ingredientList {
    display: flex;
    border-bottom: solid grey;
    align-items: center;
    width: 95%;
    padding: .5rem;
    margin: auto;
}

.amount {
    display: flex;
    border: solid rgb(207, 207, 207);
    border-radius: .5rem;
    margin: auto;
    justify-content: center;
    align-items: center;
}

.amountButton {
    width: 2rem;
    height: 60%;
    border-radius: .5rem;
    background-color: #f2f2f2;
    border: solid rgb(244, 244, 244);
}

.deleteButton {
    border-radius: .5rem;
    height: 2rem;
    background-color: #FD7E14;
    color: white;
    border: none
}

.refrigeratorCategory {
    margin-top: 2rem;
}

.storageRadio {
    display: flex;
    margin-top: 3rem;
    margin-left: 7.5rem;
}

.categoryTitle {
    text-align: start;
    margin-left: 9rem;
    font-size: 2rem;
    font-weight: bold;
}
</style>